.toggle-theme {
  width: 54px;
  height: 29px;
  background-color: var(--white-color);
  cursor: pointer;
  border-radius: 20px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
}

.toggle-theme.active {
  background-color: var(--dark-blue);
}

.toggle-theme:before {
  content: "";
  width: 25px;
  height: 25px;
  background-color: var(--dark-blue);
  background-image: url("sun.863539a9.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 17px;
  border-radius: 50%;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 2px;
  left: 2px;
}

.toggle-theme.active:before {
  background-color: var(--white-color);
  background-image: url("moon.8f3d5eb2.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 17px;
  left: calc(100% - 27px);
}

@media screen and (min-width: 768px) {
  .header-container {
    justify-content: space-between;
  }

  .nav {
    display: block;
  }

  .nav-list .nav-item:nth-child(4) {
    display: none;
  }

  .nav-list {
    height: 18px;
