  }
}

@media screen and (min-width: 1440px) {
  .header-container {
    max-width: 1170px;
  }

  .nav-list .nav-item:nth-child(4) {
    display: block;
  }

  .nav-list {
    width: 504.3px;
  }

  .nav-item:hover .had-nav-link, .nav-item:focus .had-nav-link {
    color: var(--dark-blue);
    transition: all .25s cubic-bezier(.4, 0, .2, 1);
  }

